#category-showcase {
  font-family: "IBM Plex Sans", "PT Sans", "Helvetica", sans-serif; }
  #category-showcase ul {
    display: flex;
    list-style: none;
    justify-content: space-between; }
    #category-showcase ul li {
      font-size: 1rem;
      flex-grow: 1;
      background: #f8f8f8;
      position: relative;
      margin-right: 64px;
      border-radius: 5px;
      color: #1f1f1f; }
      #category-showcase ul li a {
        display: block;
        position: relative;
        width: 100%;
        padding: 80px;
        font-weight: bolder;
        letter-spacing: -1px; }
      #category-showcase ul li a::after {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: transparent;
        content: '';
        border: 2px solid #1f1f1f;
        border-radius: 5px;
        transform: scale(1);
        opacity: 1;
        transition: transform 200ms cubic-bezier(0.4, 0, 0.2, 1); }
      #category-showcase ul li a:hover::after {
        transform: scale(0.75);
        opacity: 1;
        border: 5px solid #359df1; }
      #category-showcase ul li a:hover {
        color: #359df1; }
    #category-showcase ul li:hover {
      border-color: transparent; }
    #category-showcase ul li:last-of-type {
      margin-right: 0; }

