@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans:200,400,400i,700);
nav {
  align-self: center;
  margin-right: 5%; }
  nav ul {
    list-style: none;
    display: flex;
    justify-content: space-between; }
    nav ul li {
      display: inline-block;
      flex-grow: 1;
      margin-right: 20px;
      position: relative;
      z-index: 10; }
      nav ul li a {
        display: block;
        text-align: center;
        width: 100%;
        padding: 8px;
        font-weight: bolder;
        transition: all 150ms ease-out; }
      nav ul li a:hover {
        color: #359df1; }
    nav ul li:last-of-type {
      margin-right: 0; }
  nav #active-tracker {
    opacity: 0;
    background: #1f1f1f;
    position: absolute;
    z-index: 1;
    transition: all 200ms ease-in; }

header {
  font-family: 'IBM Plex Sans';
  padding: 8px 0;
  display: flex;
  justify-content: space-between;
  background: #f8f8f8;
  color: #1f1f1f;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10;
  opacity: 0.95; }
  header #brand-name {
    letter-spacing: -1px;
    cursor: pointer; }

#category-showcase {
  font-family: "IBM Plex Sans", "PT Sans", "Helvetica", sans-serif; }
  #category-showcase ul {
    display: flex;
    list-style: none;
    justify-content: space-between; }
    #category-showcase ul li {
      font-size: 1rem;
      flex-grow: 1;
      background: #f8f8f8;
      position: relative;
      margin-right: 64px;
      border-radius: 5px;
      color: #1f1f1f; }
      #category-showcase ul li a {
        display: block;
        position: relative;
        width: 100%;
        padding: 80px;
        font-weight: bolder;
        letter-spacing: -1px; }
      #category-showcase ul li a::after {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: transparent;
        content: '';
        border: 2px solid #1f1f1f;
        border-radius: 5px;
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
        transition: -webkit-transform 200ms cubic-bezier(0.4, 0, 0.2, 1);
        transition: transform 200ms cubic-bezier(0.4, 0, 0.2, 1);
        transition: transform 200ms cubic-bezier(0.4, 0, 0.2, 1), -webkit-transform 200ms cubic-bezier(0.4, 0, 0.2, 1); }
      #category-showcase ul li a:hover::after {
        -webkit-transform: scale(0.75);
                transform: scale(0.75);
        opacity: 1;
        border: 5px solid #359df1; }
      #category-showcase ul li a:hover {
        color: #359df1; }
    #category-showcase ul li:hover {
      border-color: transparent; }
    #category-showcase ul li:last-of-type {
      margin-right: 0; }

