.sound-container {
  font-family: "IBM Plex Sans", "PT Sans", "Helvetica", sans-serif;
  margin: 0px 0;
  display: flex;
  padding: 8px;
  padding-right: 16px; }
  .sound-container .sound-left {
    flex-grow: 1;
    font-weight: lighter;
    margin-left: 40px; }
  .sound-container .sound-right {
    align-self: center; }
  .sound-container .sound-name {
    font-weight: normal;
    font-size: 18px;
    color: #1f1f1f;
    line-height: 100%; }
    .sound-container .sound-name a:hover {
      text-decoration: underline; }
  .sound-container .sound-play.button {
    display: inline-block;
    font-size: 14px;
    border-radius: 100px;
    width: 32px;
    height: 32px;
    text-align: center;
    align-self: center;
    margin-right: 8px;
    display: none;
    padding-left: 12px;
    padding-bottom: 23px; }
  .sound-container .sound-play.button:hover {
    background: #359df1; }
  .sound-container .sound-tags,
  .sound-container .sound-date,
  .sound-container .sound-bpm,
  .sound-container .sound-plays,
  .sound-container .sound-purchases {
    font-size: 12px; }
  .sound-container .sound-tags {
    display: inline-block; }
  .sound-container .sound-tag {
    margin-right: 4px;
    transition: all 150ms cubic-bezier(0.4, 0, 0.2, 1);
    opacity: 0.35; }
    .sound-container .sound-tag a {
      display: inline-block;
      padding: 4px 8px;
      border-radius: 2px;
      border: 1px solid #313131;
      background: transparent;
      color: #313131;
      font-size: 11px;
      letter-spacing: -0.5px;
      font-weight: normal; }
  .sound-container .sound-tag:hover {
    opacity: 1; }
  .sound-container .sound-price {
    display: inline-block;
    font-size: 14px;
    margin-left: 16px;
    font-weight: bolder; }

.sound-container:hover {
  background: #ebebeb; }
  .sound-container:hover .sound-left {
    margin-left: 0; }
  .sound-container:hover .sound-play.button {
    display: block; }
